body{
    text-align: center;
    min-width: 380px;
    background-color: #003300;
}
.header{
    background-color: #eaffea;
    display: flex;
    height: auto;
    flex-direction: column;
    justify-content: center;
}
.header > img{
    align-self: center;
}
h1{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: 300;
    font-size: 2.5em;
    letter-spacing: -5px;
}
.header > h1{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: 300;
    font-size: 2.5em;
    letter-spacing: -5px;
    /*line-height: 0%;*/
}
p{
    font-family: 'Roboto Mono', sans-serif;
    font-weight: normal;
    font-size: 1.2rem;
}
.navbar{
    position: fixed;
    background-color: #003300;
    align-content: center;
    top: 0px;
    right: 0px;
}
.navbar > ul{
    display: flex;
    margin: 10px;
    padding: 10px;
}
.navbar > li{
    display: block;
    list-style: none;
}
.link{
    background-color: #003300;
    color: #eaffea;
    padding: 10px;
}
.link:visited{
    background-color: #003300;
    color: #eaffea;
}
.link:hover{
    background-color: #eaeaea;
    color: #003300;
}
.rodape{
    position: fixed;
    background-color: #003300;
    color: #eaffea;
    bottom: 0px;
    right: 0px;
}
.rodape > p{
    font-family: 'Roboto Mono', sans-serif;
    font-size: 12px;
    font-weight: 300;
}
#projetos > p {
    list-style: none;
    text-align: left;
    padding: 10px;
}
#projetos{
    width: 100%;
    height: auto;
    background-color: #daffda;
    padding-bottom: 16px;
}
#sobre{
    width: 100%;
    height: auto;
    background-color: #caffca;
}
#sobre > p{
    text-align: left;
    padding: 0px 10px 10px 10px;
}

@media(max-width: 750px){
    .header > img{
        padding: 55px;
    }
    
}
@media(max-width: 500px){
    #projetos{
        padding-bottom: 0px;
    }
    .navbar {
        position: fixed;
        width: 100%;

    }
    .header > img{
        padding: 55px;
    }
    .rodape {
        position: inherit;
        white-space: pre-line;
    }
}